import React from 'react';
import {NavLink} from 'react-router-dom'

const ParteDeTrabajo = (props) =>{
  return(
    <li key={props.ptId} className="collection-item avatar">
      <i className="material-icons circle">person</i>
      <div className="row">
        <div className="col l2">
          <span className="title">{props.Firma_empleado}</span>
        </div>
        <div className="col l2">
            <i className="material-icons">today</i>
            {props.Fecha_firma_empleado.substring(0,10)} 
        </div>
        <div className="col l2">
            Num horas: {props.Horas} 
        </div>
        <div className="col l2">
            Estado Parte: {props.EstadoParte}
        </div>
        <div className="col l2">
          <NavLink to="#">
            <i className="material-icons medium right">zoom_in</i>
          </NavLink>
        </div>
      </div>
    </li>
  )
}

export default ParteDeTrabajo;


